/* Explore.css */

/* General Page Styles */
.explore-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

.page-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: #2c3e50;
}

/* Category Grid */
.category-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.category-card {
  background-color: #f8f9fa;
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.category-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0,0,0,0.2);
}

.category-card img.category-img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.category-card h3 {
  margin: 15px 0 5px 0;
  font-size: 1.4rem;
  color: #2c3e50;
}

.category-card p {
  font-size: 0.95rem;
  padding: 0 10px 15px 10px;
  color: #555;
}

.category-card.active {
  border: 2px solid #3498db;
}

/* Role Selection */
.role-selection {
  text-align: center;
  margin-bottom: 30px;
}

.role-selection h2 {
  font-size: 1.6rem;
  margin-bottom: 15px;
}

.role-selection button {
  margin: 0 10px;
  padding: 10px 25px;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  background-color: #3498db;
  color: #fff;
  transition: all 0.3s ease;
}

.role-selection button:hover {
  background-color: #2980b9;
}

.role-selection button.active {
  background-color: #2ecc71;
}

/* Form Styles */
.role-form-container {
  background-color: #f8f9fa;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  margin-bottom: 40px;
}

.role-form-container h2 {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 20px;
}

.form-section {
  margin-bottom: 20px;
}

.form-section h3 {
  margin-bottom: 10px;
  color: #34495e;
}

.form-section input {
  width: 100%;
  padding: 10px 12px;
  margin: 6px 0;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

.submit-btn {
  display: block;
  margin: 20px auto 0 auto;
  padding: 12px 35px;
  font-size: 1.1rem;
  border: none;
  border-radius: 10px;
  background-color: #2ecc71;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-btn:hover {
  background-color: #27ae60;
}

/* Search Results */
.search-results {
  margin-top: 30px;
}

.search-results h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #2c3e50;
}

.search-card {
  background-color: #fff;
  padding: 15px 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  margin-bottom: 15px;
  transition: all 0.3s ease;
}

.search-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 15px rgba(0,0,0,0.15);
}

.search-card h3 {
  font-size: 1.2rem;
  margin-bottom: 5px;
  color: #3498db;
}

.search-card p {
  font-size: 0.95rem;
  color: #555;
}

.map-container {
  margin-top: 30px;
  padding: 15px;
  border-radius: 12px;
  background-color: #f8f9fa;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.map-container h2 {
  text-align: center;
  margin-bottom: 15px;
  color: #2c3e50;
}
